@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400&display=swap');

/* 1d2026 */

#start {
  position:relative;
  display: block;
  width: 100vw;
  height: 100vh;
}

body {
  background-color: #1c1c1c;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  top:0;
  margin:0;
  overflow-x:hidden;
}

h2 {
  z-index:13;
  font-family:'Poppins';
  font-size:92px;
  color:black;
  display:inline-block;
  padding:0;
  margin:0px;
  top:30px;
  left:40px;
  position: absolute;
  -webkit-user-select: none;
}

/* Menlo Font to be use on site */

.mono {font-family: Menlo;}


/* [DOCUMENTATION] text */

#doc {
  font-size:12px;
  position: absolute;
  top:10px;
  right: 10px;
  z-index:13;
  margin:0;
  color:rgba(0, 0, 0, 0.5)
}

/* software engineer. text */

#SWE {
  font-size:20px;
  color:rgba(0, 0, 0, 1);
  position:absolute;
  z-index: 13;
  top: 150px;
  left: 40px;
  margin:0;
}

/* [click anywhere to proceed label] */

#label {
  position: absolute;
  color: rgba(0, 0, 0, 0.5);
  z-index:13;
  font-size: 12px;
  margin: 0;
  top: 50%;
  left: 50%;
  -moz-transform: translateX(-50%) translateY(-50%);
  -webkit-transform: translateX(-50%) translateY(-50%);
  transform: translateX(-50%) translateY(-50%);
}

/* intial background gradient */

#top-gradient {
  position: absolute;
  width: 100vw;
  height:100%;
  top: 0px;
  margin:0;
  background: linear-gradient(155deg, #FFE600 1.26%, #00FBFF 97.48%);
  background-size: 200% 100%;
  z-index:12;
  animation: move 5s ease infinite;
}

/* clickable circle */

#response {
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.0);
  position: absolute;
  z-index: 14;
  top: 0px;
  border: 0px;
  margin:auto;
  font-size:12px;
  color:rgba(0, 0, 0, 0.5)
}

