/* Top Gradient Movement */

@keyframes move {
	0% {
		background-position: 0% 50%;
	}
	50% {
		background-position: 100% 50%;
	}
	100% {
		background-position: 0% 50%;
	}
}

/* Text Highlight Color */

::selection {
  background:rgba(255, 255, 255, 0.2);
}

/* Responsive Design Rules */

@media only screen and (max-width: 600px) {
  h2 {
    font-size: 15vw;
  } 
}
